import React from 'react'
import { Form,Button } from 'react-bootstrap'



export default function CategoryForm ({value,setValue,handleSubmit}) {
  return (
    <div>
      <Form onSubmit={handleSubmit} style={{width:"50%",marginLeft:"20%"}}>

<Form.Group className="mb-3" controlId="formBasicCategory">
  <Form.Label>Update Category</Form.Label>
  <Form.Control value={value} onChange={(e)=>setValue(e.target.value)} style={{border:"1px solid skyblue"}} type="category" placeholder="Password" />
</Form.Group>
<Button variant="primary" type="submit">
  Update
</Button>
</Form>
    </div>
  )
}


