import React, { useEffect, useState } from 'react'
import Panel from '../components/Panel'
import { Button, Table } from 'react-bootstrap'
import axios from 'axios'
import { Modal } from 'antd'
import CategoryForm from '../components/CategoryForm'

const UpdateCategory = () => {
    const [categories , setCategories] = useState()
    const [visibl , setVisibl] = useState()
    const [updatedName , setUpdatedName] = useState()
    const [selected , setSelected] = useState()
         
      const handleUpdate=async()=>{
        
      }

    const getAllCategory= async()=>{
      const {data} = await axios.get("/api/v1/category/get-category")
      if(data.success){
        setCategories(data.category)
      }
    }

    useEffect(()=>{
      getAllCategory()
    },[])
  return (
    <div>
      
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-4"><Panel/></div>
          <div className="col-md-8">

          <Table style={{margin:"15px"}} striped bordered hover size="sm">
      <thead>
        <tr>
          <th>Name</th> <br />
          <th>Actions</th> <br />
          <th>Actions</th> <br />
        </tr>
      </thead>
      <tbody style={{margin:"10px"}}>
       {
        categories?.map(c=>(
            <tr>
            <td style={{margin:'5px'}}>{c.name}</td> <br />
            <td style={{margin:'5px'}}><Button onClick={()=>{setVisibl(true); setUpdatedName(c.name); }} className='btn btn-primary' >Edit</Button></td> <br />
            <td style={{margin:'5px'}}><Button className='btn btn-danger' >Delete</Button></td> <br />
           
          </tr>
        ))
       }
        
      </tbody>
    </Table>
          </div>
        </div>
    </div>
    <Modal footer={null} onCancel={()=>setVisibl(false)} visible={visibl} >
        <CategoryForm value={updatedName} setValue={setUpdatedName} handleSubmit={handleUpdate} />
    </Modal>
    </div>
  )
}

export default UpdateCategory
