import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Col, Row } from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Nav from 'react-bootstrap/Nav';
import { Link } from 'react-router-dom';


const AllProducts =()=> {
  const [product , setProduct] = useState();
    const getAllProducts = async()=>{
        const {data} = await axios.get("/api/v1/product/get-product")
        if(data.success){
            setProduct(data.product)
        }
    }

    useEffect(()=>{
        getAllProducts()
    },[])
  return (
    <div>
        {
        product?.map(p=>(
            <Card style={{ width: '280px',paddingInline:"4px",marginInline:'4px', height:"330px", marginLeft:"23px",marginTop:"12px", borderBlockStyle:"revert", display:"inline-block", margin:"7px", padding:"10px"  }}>
            <Card.Img style={{height:"", width:""}} variant="top" src={`/api/v1/product/get-photo/${p._id}`}
            alt={p.name} />
            <Card.Body>
              <Card.Title>{p.title}</Card.Title>
              <Card.Text >
              <h6><Col md={8}>Price:$ {p.price}</Col></h6>  
              Buy:<Link to={p.link}>{p.link} </Link>
        </Card.Text>
              
            </Card.Body>
          </Card>
        ))
      }
       
      
    </div>
  )
}

export default AllProducts
