import axios from 'axios';
import React, { useState } from 'react'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { useNavigate } from 'react-router';
import { useAuth } from '../context/auth';


const Login = () => {

  
  const [email , setEmail]= useState("")
  const [password , setPassword]= useState("")
  const [auth , setAuth] = useAuth()

  const navigate = useNavigate()

const handleSubmit=async(e)=>{
  try {
    e.preventDefault()
    const {data} = await axios.post("/api/v1/auth/login" , {email,password})
    if(data.success){
           setAuth({
            ...auth ,
            user :data.user,
            token:data.token
           })
           localStorage.setItem("auth",JSON.stringify(data))
           navigate("/home")
    }

  } catch (error) {
    console.log(error)
    
  }
}
  return (
    <div>
      <h1 style={{marginLeft:'20%',marginTop:"20px"}}>Login</h1>
      <Form onSubmit={handleSubmit} style={{width:"50%",marginLeft:"20%"}}>
      <Form.Group  className="mb-3" controlId="formBasicEmail">
        <Form.Label>Email address</Form.Label>
        <Form.Control  value={email} onChange={(e)=>setEmail(e.target.value)} style={{border:"1px solid skyblue"}} type="email" placeholder="Enter email" />
        <Form.Text className="text-muted">
          We'll never share your email with anyone else.
        </Form.Text>
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label>Password</Form.Label>
        <Form.Control value={password} onChange={(e)=>setPassword(e.target.value)} style={{border:"1px solid skyblue"}} type="password" placeholder="Password" />
      </Form.Group>
      <Button variant="primary" type="submit">
        Submit
      </Button>
    </Form>
    </div>
  )
}

export default Login
