import React from 'react'
import ListGroup from 'react-bootstrap/ListGroup';
import { Link } from 'react-router-dom';

const Panel = () => {
  return (
    <div>
      <ListGroup style={{width:'34%',marginTop:'7%'}} defaultActiveKey="#link1">
      <ListGroup.Item action as={Link} to="/create-p">
        Add Product
      </ListGroup.Item>
      <ListGroup.Item action as={Link} to="/create-c" >
        Add Category
      </ListGroup.Item>
      <ListGroup.Item action as={Link} to="/update-c" >
        Update Category
      </ListGroup.Item>
      <ListGroup.Item action as={Link} to="/update-p" >
        Update Product
      </ListGroup.Item>
    </ListGroup>
    </div>
  )
}

export default Panel
