import { Route, Routes } from 'react-router';
import './App.css';
import Header from './components/Header';
import Login from './pages/Login';
import Register from './pages/Register';
import Home from './pages/Home';

import PrivateRoute from './Routes/Private';
import AdminRoute from "./Routes/AdminRoute"

import AboutUs from './pages/AboutUs';
import AdminDashboard from './pages/AdminDashboard';
import Dashboard from './pages/Dashboard';
import AllProducts from './pages/AllProducts';
import AddProduct from './pages/AddProduct';
import AddCategory from './pages/AddCategory';
import SearchP from './pages/SearchP';
import UpdateCategory from './pages/UpdateCategory';
import UpdateProduct from './pages/UpdateProduct';

function App() {
  return (
    <div>
        <Header/> 
        <Routes>
          <Route path='/login' element={<Login/>}/>
          <Route path='/search' element={<SearchP/>}/>
          <Route path='/register' element={<Register/>}/>
          <Route path='/home' element={<Home/>}/>
         <Route path='/create-c' element={<AddCategory/>}/>
         <Route path='/update-p' element={<UpdateProduct/>}/>
         <Route path='/update-c' element={<UpdateCategory/>}/>
         <Route path='/create-p' element={<AddProduct/>}/>
          <Route path='/dashboard' element={<PrivateRoute/>}>
               <Route path='user' element={<Dashboard/>}/>
          </Route>
          <Route path='/dashboard' element={<AdminRoute/>}>
               <Route path='admin' element={<AdminDashboard/>}/>
          </Route>
          <Route path='/about' element={<AboutUs/>}/>
          <Route path='/products' element={<AllProducts/>}/>
        </Routes>
        
    </div>
      
  ) 
      
}

export default App;
