import React from 'react'
import { useSearch } from '../context/search'
import { Col, Row } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import { Link } from 'react-router-dom';


const SearchP = () => {
    const [values,setValues] = useSearch()

  return (
   <div className="container">
    <div className="text-center">
        <h1 style={{marginTop:'6px'}}>Search Reasults</h1>
        <h6>{values?.results.length < 1 ? "No Product found" : ""}</h6>
        <div className="d-flex flex-wrap mt-4 ">
        {
        values?.results?.map(p=>(
            <Card style={{ width: '280px',paddingInline:"4px",marginInline:'4px', height:"330px", marginLeft:"23px",marginTop:"12px", borderBlockStyle:"revert", display:"inline-block", margin:"7px", padding:"10px"  }}>
            <Card.Img style={{height:"", width:""}} variant="top" src={`/api/v1/product/get-photo/${p._id}`}
            alt={p.name} />
            <Card.Body>
              <Card.Title>{p.name}</Card.Title>
              <Card.Text >
              <h6><Col md={8}>Price:$ {p.category.name}</Col></h6>  
              Buy:<Link to={p.link}>{p.link} </Link>
        </Card.Text>
              
            </Card.Body>
          </Card>
        ))
      }
        </div>
    </div>
   </div>
  )
}

export default SearchP
