import { Checkbox } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Col, NavLink, Row } from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Nav from 'react-bootstrap/Nav';
import { Link } from 'react-router-dom';


const Home=()=> {
    const [product , setProduct] = useState();
    const [categories , setCategories] = useState()

    const getAllCategory= async()=>{
      const {data} = await axios.get("/api/v1/category/get-category")
      if(data.success){
        setCategories(data.category)
      }
    }

    useEffect(()=>{
      getAllCategory()
    },[])


    const getAllProducts = async()=>{
        const {data} = await axios.get("/api/v1/product/get-product")
        if(data.success){
            setProduct(data.product)
        }
    }

    useEffect(()=>{
        getAllProducts()
    },[])


  return (
    <div>
     <Row>
      <Col sm={3} style={{}} >{
        categories?.map(c=>(
               <Checkbox style={{marginLeft:'21px',marginTop:"10px",font:"caption", padding:"5px", display:"flex"}} key={c._id}>{c.name}</Checkbox>
        ))
      }</Col>
      <Col md={9} style={{}}> {
        product?.map(p=>(
            <Card style={{ width: '280px', height:"330px", marginLeft:"-8px",paddingInline:"4px",marginInline:'4px',marginTop:"12px", borderBlockStyle:"revert", display:"inline-block", margin:"11px", padding:"10px"  }}>
            <Card.Img style={{height:"", width:""}} variant="top" src={`/api/v1/product/get-photo/${p._id}`}
            alt={p.name} />
            <Card.Body>
              <Card.Title>{p.title}</Card.Title>
              <Card.Text >
              <h6><Col md={8}>Price:$ {p.price}</Col></h6>  
              Buy:<Link to={p.link}>{p.link} </Link>
        </Card.Text>
              
            </Card.Body>
          </Card>
        ))
      }</Col>
     </Row>
    </div>
  )
}

export default Home
