import React, { useEffect, useState } from 'react'
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useSearch } from '../context/search';
import axios from 'axios';
import { useNavigate } from 'react-router';
import { Dropdown } from 'bootstrap';

const SearchInput = () => {
    const [values,setValues] = useSearch()
    const navigate = useNavigate()

    const haldleSearch=async(e)=>{
            e.preventDefault();
           try {
            const {data} = await axios.get(`/api/v1/product/search/${values.keyword}`)
            setValues({...values,results:data})
            navigate("/search")
           } catch (error) {
            console.log(error)
           }
    }
    const [product , setProduct] = useState();
    const getAllProducts = async()=>{
        const {data} = await axios.get("/api/v1/product/get-product")
        if(data.success){
            setProduct(data.product)
        }
    }

    useEffect(()=>{
        getAllProducts()
    },[])
        
    const onSearch = (searchTerm)=>{
        console.log("search",searchTerm)
    }

  return (
    <div>
      <Form  onSubmit={haldleSearch} style={{marginRight:"9px",marginTop:'5px'}}>

      
        <Row>
          <Col xs="auto">
            <Form.Control style={{width:'270px'}} value={values.keyword} onChange={(e)=>setValues({...values,keyword:e.target.value})}
              type="text"
              placeholder="Search"
              className=" mr-sm-2"

            >
            </Form.Control>
            {
                product?.map(p=>{
                    <div>{p.title}</div>
                })
            }
            
          </Col>
          
          <Col xs="auto">
            <Button onClick={()=>onSearch(values.keyword)} type="search">Search</Button>
          </Col>
        </Row>
      </Form>
    </div>
  )
}

export default SearchInput
