import axios from 'axios';
import { Toast } from 'bootstrap';
import React, { useState } from 'react'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { useNavigate } from 'react-router';

const Register = () => {
  const [name , setName]= useState("")
  const [email , setEmail]= useState("")
  const [phone , setPhone]= useState("")
  const [password , setPassword]= useState("")
  const navigate = useNavigate()

const handleRegister=async(e)=>{
  try {
    e.preventDefault()
    const {data} = await axios.post("/api/v1/auth/register" , {name,email,phone,password})
    if(data.success){
          navigate("/login")
    }

  } catch (error) {
    console.log(error)
    
  }
}

  return (
    <div>
      <h1 style={{marginLeft:"20%", marginTop:'20px'}}>Register</h1>
      <Form onSubmit={handleRegister} style={{width:"50%",marginLeft:"20%"}}>
      <Form.Group    className="mb-3" controlId="formBasicName">
        <Form.Label>Name</Form.Label>
        <Form.Control value={name} onChange={(e)=>setName(e.target.value)} style={{border:"1px solid skyblue"}} type="name" placeholder="Enter Name" />
      </Form.Group>

      <Form.Group  className="mb-3" controlId="formBasicEmail">
        <Form.Label>Email address</Form.Label>
        <Form.Control value={email} onChange={(e)=>setEmail(e.target.value)} style={{border:"1px solid skyblue"}} type="email" placeholder="Enter email" />
        <Form.Text className="text-muted">
          We'll never share your email with anyone else.
        </Form.Text>
      </Form.Group>

      <Form.Group  className="mb-3" controlId="formBasicPhone">
        <Form.Label>Phone</Form.Label>
        <Form.Control value={phone} onChange={(e)=>setPhone(e.target.value)} style={{border:"1px solid skyblue"}} type="Phone" placeholder="Enter Phone" />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label>Password</Form.Label>
        <Form.Control value={password} onChange={(e)=>setPassword(e.target.value)} style={{border:"1px solid skyblue"}} type="password" placeholder="Password" />
      </Form.Group>
      <Button variant="primary" type="submit">
        Submit
      </Button>
    </Form>
    </div>
  )
}

export default Register
