import React from 'react'
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/auth';
import SearchInput from './SearchInput';


const Header = () => {
  
  const [auth , setAuth] = useAuth()
  const navigate = useNavigate()
  const handleLogout=(e)=>{
    e.preventDefault()
    setAuth({
      ...auth,
      user:null,
      token:""
    })
    localStorage.removeItem("auth")
    navigate('/login')
  }
  return (
    <div>
      <Navbar expand="lg" className="navbars">
    
      <Container>
        <Navbar.Brand style={{color:"white",padding:"6px"}} href="#home">OFFER-LUX</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <SearchInput/>
            <Nav.Link style={{color:"black",marginLeft:'12px'}} as={Link} to="/home">Home</Nav.Link>
            <Nav.Link style={{color:"black"}} as={Link} to="/products">All Products </Nav.Link>
            <Nav.Link style={{color:"black"}} as={Link} to="/about">About Us</Nav.Link>
            <Nav.Link style={{color:"black"}} as={Link} to={`/dashboard/${auth?.user?.role === 1 ? "admin" : "user"}`}>Dashboard</Nav.Link>
            
            {
              !auth.user ? ( <>
              <Nav.Link style={{color:"black"}} as={Link} to="/login">Login</Nav.Link>
            <Nav.Link style={{color:"black"}} as={Link} to="/register">Register</Nav.Link>
              </>) : (<>
                <Nav.Link onClick={handleLogout} style={{color:"black"}} as={Link} to="/logout">Logout</Nav.Link></>)
            }
           
            
            
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
    </div>
  )
}

export default Header
