import React, { useEffect, useState } from 'react'
import Panel from '../components/Panel'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import axios from 'axios';
import { useNavigate } from 'react-router';

const AddProduct = () => {
    const [categories,setCategories] = useState()
    const [title , setTitle] = useState("")
    const [description , setDescription] = useState("")
    const [price , setPrice] = useState("")
    const [link , setLink] = useState("")
    const [keys , setKeys] = useState("")
    const [category , setCategory] = useState("")
    const [photo , setPhoto] = useState("")


    const getAllCategory= async()=>{
        const {data} = await axios.get("/api/v1/category/get-category")
        if(data.success){
          setCategories(data.category)
        }
      }
  
      useEffect(()=>{
        getAllCategory()
      },[])
       
      const navigate = useNavigate()


    const handleCreate =async(e)=>{
          e.preventDefault()
          const productData = new FormData()
          productData.append("title" , title)
          productData.append("description" , description)
          productData.append("price" , price)
          productData.append("link" , link)
          productData.append("keys" , keys)
          productData.append("category" , category)
          productData.append("photo" , photo)

          const {data} = await axios.post("/api/v1/product/create-product", productData )
          if(data.success){
                navigate("/home")
          }


    }

  return (
    <div>
       <div style={{padding:'20px'}} className="container-fluid">
        <div className="row">
          <div className="col-md-4"><Panel/></div>
          <div className="col-md-8">
          <h1 style={{marginLeft:"-8%", marginTop:'20px'}}>Add Product</h1>
      <Form onSubmit={handleCreate} style={{width:"50%",marginLeft:"20%"}}>
      <Form.Group    className="mb-3" controlId="formBasicName">
        <Form.Label>Name</Form.Label>
        <Form.Control value={title} onChange={(e)=>setTitle(e.target.value)} style={{border:"1px solid skyblue"}} type="name" placeholder="Enter Name" />
      </Form.Group>

      <Form.Group  className="mb-3" controlId="formBasicDescription">
        <Form.Label>Description</Form.Label>
        <Form.Control value={description} onChange={(e)=>setDescription(e.target.value)} style={{border:"1px solid skyblue"}} type="description" placeholder="Enter description" />
      </Form.Group>

      <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
        <Form.Label>Keys</Form.Label>
        <Form.Control value={keys} onChange={(e)=>setKeys(e.target.value)} as="textarea" rows={3} />
      </Form.Group>

      <Form.Group  className="mb-3" controlId="formBasicPrice">
        <Form.Label>Price</Form.Label>
        <Form.Control value={price} onChange={(e)=>setPrice(e.target.value)} style={{border:"1px solid skyblue"}} type="Price" placeholder="Enter Price" />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicLink">
        <Form.Label>Link</Form.Label>
        <Form.Control value={link} onChange={(e)=>setLink(e.target.value)} style={{border:"1px solid skyblue"}} type="link" placeholder="Enter Link" />
      </Form.Group>

      <input type="file" name="photo" accept='image/*' onChange={(e)=>setPhoto(e.target.files[0])}  /> <br /><br />

        <div  className="m-1 mb-3" >
            <select value={category} onChange={(e)=>setCategory(e.target.value)}>
                   {
                    categories?.map(c=>(
                        <option value={c._id} key={c._id} >{c.name}</option>
                    ))
                   }
            </select>
        </div>

      <Button style={{marginBottom:'25px'}} variant="primary" type="submit" >
        Create
      </Button>
    </Form>
          
          </div>
        </div>
      </div>
    </div>
  )
}

export default AddProduct
