import React, { useState } from 'react'
import Panel from '../components/Panel'
import { Form,Button } from 'react-bootstrap'
import axios from 'axios'

const AddCategory = () => {
  const [name , setName] = useState()
   
const handleSubmit=async(e)=>{
  e.preventDefault()
  const {data} = await axios.post("/api/v1/category/create-category" , {name})
  if(data.success){
    alert("successfull")
  }
}
  return (
    <div>
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-4"><Panel/></div>
          <div className="col-md-8"><h1 style={{marginLeft:'20%',marginTop:"20px"}}>Add category</h1>
      <Form onSubmit={handleSubmit} style={{width:"50%",marginLeft:"20%"}}>

      <Form.Group className="mb-3" controlId="formBasicCategory">
        <Form.Label>Add Category</Form.Label>
        <Form.Control value={name} onChange={(e)=>setName(e.target.value)} style={{border:"1px solid skyblue"}} type="category" placeholder="Password" />
      </Form.Group>
      <Button variant="primary" type="submit">
        Create
      </Button>
    </Form></div>
        </div>
      </div>
    </div>
  )
}

export default AddCategory
